.dialogs {
    display: grid;
    grid-area: content;
    grid-template-columns: 2fr 10fr;
    height: 100%;
    margin-bottom: 5%;
}

.dialogsTitle {
    text-align: center;
    padding: 1%;
    margin-bottom: 5%;
}

.dialogsUsers {
    border-radius: 25px;
    border: solid 1px rgba(0, 0, 0, 0.28);
    margin-bottom: 5%;
    background: linear-gradient(to bottom right, rgba(29, 29, 29, 0.59), rgba(68, 94, 95, 0.53), rgba(18, 49, 7, 0.32));
}

.dialogDiv {
    padding: 10px;
    border: solid 1px rgba(0, 0, 0, 0.26);
    margin: 5px;
    border-radius: 15px;
    text-align: center;
    background: linear-gradient(to bottom right, rgba(29, 29, 29, 0.59), rgba(68, 94, 95, 0.53), rgba(18, 49, 7, 0.32));
}

.dialogItem {
    padding: 20px;
    margin: 10px;
    border-radius: 15px;
    text-decoration: none;
    color: azure;
    text-align: center;
}

.active {
    text-decoration: none;
    color: darkgoldenrod;
    font-size: 120%;
    padding: 10px;
    text-align: center;
}

.titleMessages {
    text-align: center;
}

.messages {
    display: grid;
    grid-template-rows: 1fr 6fr 2fr;
    padding: 10px;
    border-radius: 25px;
    border: solid 1px rgba(0, 0, 0, 0.29);
    margin: 0 1% 1% 1%;
    background: linear-gradient(to bottom right, rgba(29, 29, 29, 0.59), rgba(68, 94, 95, 0.53), rgba(18, 49, 7, 0.32));
}

.addMessage {
    margin: 2%;
}

.inputAddMessage {
    float: left;
    width: 100%;
    grid-area: input;
    border-radius: 15px;
    border: solid 2px rgba(0, 0, 0, 0.55);
    background: linear-gradient(to bottom right, rgba(255, 171, 171, 0.29), rgba(255, 255, 255, 0.54), rgba(155, 255, 121, 0.3));
}

.buttonAddMessage {
    float: right;
    width: 50%;
    grid-area: button;
    border-radius: 15px;
    background: rgba(0, 0, 0, 0.43);
    color: azure;
    border: solid 2px rgba(255, 255, 255, 0.55);
    padding: 10px;
}

.divAddMessage {
    display: grid;
    grid-template-areas: 'input c button';
    grid-template-columns: 20fr 1fr 4fr;
}

.iconMessage {
    height: 40px;
    width: 40px;
    border-radius: 50%;
    border: solid 1px black;
}

.message {
    padding: 10px;
}

.divMessage {
    display: grid;
    grid-template-columns: 1fr 15fr;
    padding: 10px;
}